/* reservation information component */

.room-info-display-component {
  width: 500px;
  min-height: 750px;
  margin: 0px 0px 0px 15px;
  padding: 0px;
  box-shadow: -2px 2px 10px #888888;
  border-radius: 10px;
  border: 3px outset black;
  background-color: #E9E8E8;
}

.ammenities-list {
  list-style:none;
}

.reservation-info{
  padding-left: 10px;
}

/* equipment component */
.equipment-component {
  width: 500px;
  height: 500px;
  margin: 0px 0px 0px 15px;
  padding: 0px;
  box-shadow: -2px 2px 10px #888888;
  border-radius: 10px;
  border: 3px outset black;
  background-color: #E9E8E8;
}

.equipment-content {
  padding-left: 15px;
  height: 390px;
}

.select-button-wrapper {
  display: flex;
  justify-content: center;
}

.select-button {
  background-color: #008CBA;
  color: white;
  font-size: 18px;
  border-radius: 6px;
}

/* equipment modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  width: 300px;

  height: auto;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}

.equipment-container {
  display: flex;
  flex-direction: column;
  margin: 5px;
  min-height: 200px;
}

.confirm-button{
  padding: 0px;
  margin: 0px;
  width: 500px;
  height: 100px;
}

/* shared classes */
.float-right {
  float: right;
}

.float-left {
  float: left;
}

.display-info{
  padding-left: 70px;
}

.black-header {
  color: white;
  background-color: black;
  margin: 0px;
  padding-bottom: 5px;
  text-align: center;
}

.not-visible {
  visibility: hidden;
}
